import React from 'react'

function Background(props){
    var description = props.info.weather[0].main
    var imgURL = "https://source.unsplash.com/"+window.screen.width+"x"+window.screen.height+"?"+description+""
    return(
        <div className="background_img" style={{backgroundImage:`url(${imgURL})`}}>
        </div>
    )
}

export default Background