import React, { useState, useEffect } from 'react'

function Secondary(props){
    const [loaded, setLoaded] = useState(false)
    const { day,id,details,index } = props
    useEffect(()=>{
        if(details){
            setLoaded(true)
        }
        else console.log("Waiting for details for <Secondary/> Component...")
    },[details])
    return(
        <div className="opacity_one">
            <div className="opacity_two" id={id}>
                <div className="secondary_day">{id==="one"? "tomorow":day}</div>
                <img className="secondary_logo" src={loaded?`http://openweathermap.org/img/wn/${details[index].weather[0].icon}@2x.png`:"http://openweathermap.org/img/wn/04n@2x.png"} alt="logo"/>
                <div className="secondary_temp"><b>{loaded?Math.round(details[index].main.temp_max-273.15)+"/"+Math.round(details[index].main.temp_min-273.15):"30&deg;/26&deg;"}</b></div>
            </div>
            <div className="secondary">
                <div className="secondary_day">{id==="one"? "tomorow":day}</div>
                <img className="secondary_logo" src={loaded?`http://openweathermap.org/img/wn/${details[index].weather[0].icon}@2x.png`:"http://openweathermap.org/img/wn/04n@2x.png"} alt="logo"/>
                <div className="secondary_temp"><b>{loaded?Math.round(details[index].main.temp_max-273.15)+"/"+Math.round(details[index].main.temp_min-273.15):"30&deg;/26&deg;"}</b></div>
            </div>
        </div>
    )
}

export default Secondary
