import React from 'react'

function Primary(props){

  const data = props.info
  const months = ["January","February","March","April","May","June","July","August","September","October","Novermber","December"]
  const weather = data.weather[0]
  const date = props.date_formated.split('-')
  const year = date[0]
  const month = months[parseInt(date[1]-1)]
  const day = date[2]

  const compass = (degrees) =>{
    var direction = ''
    if(direction > 360) 
      return 
    if(degrees>=337.5 || degrees<=22.5)
      return direction = "N"
    else if(degrees>22.5 && degrees<67.5)
      return direction = "NE"
    else if(degrees>=67.5 && degrees<=112.5)
      return direction = "E"
    else if(degrees>112.5 && degrees<157.5)
      return direction = "SE"
    else if(degrees>=157 && degrees<=202.5)
      return direction = "S"
    else if(degrees>202.5 && degrees<247.5)
      return direction = "SW"
    else if(degrees>=247.5 && degrees<=292.5)
      return direction = "W"
    else if(degrees>292.5 && degrees<337.5)
      return direction = "NW"
    else
      return console.log("Error... Something went wrong converting from degrees to compass direction")
  }

      return(
          <div className="primary">
          <div className="prmy_section">
            <img className="prmy-icon" src="http://openweathermap.org/img/wn/04n@2x.png" alt="weather-logo"/>
            <p className="prmy-description"><b>{weather.description}</b></p>
            </div>
          <div className="prmy_section" id="prmy_section_middle">
            <div className="prmy_section_middle_section" id="location">Cape Town <span id="country_code">za</span></div> 
            <div className="prmy_section_middle_section" id="main_temp"><b>{Math.round(data.main.temp-273.15)}&deg;</b></div> 
            <div className="prmy_section_middle_section">
              <div>Hi: <b>{Math.round(data.main.temp_max-273.15)}&deg;</b></div>
              <div>Low: <b>{Math.round(data.main.temp_min-273.15)}&deg;</b></div>  
            </div> 
          </div>
          <div className="prmy_section" id="prmy_section_end">
            <div id="date"><b>Today - {day} {month} {year}</b></div>
            <div className="prmy_section_end_section">PRECIP: <b>{data.main.precipitation ? data.main.precipitation : "0"}</b></div>
            <div className="prmy_section_end_section">HUMIDITY: <b>{Math.round(data.main.humidity)}</b></div>
            <div className="prmy_section_end_section">WINDS: <b>{compass(data.wind.deg)} {Math.round(data.wind.speed)} KPH</b></div>  
          </div>
        </div>            
      )
}

export default Primary

