import React from 'react';

import './App.css';

import weather from './weather.json'
import Primary from './components/Primary'
import Secondary from './components/Secondary'
import Filler from './components/Filler'
import Background from './components/Background'

class App extends React.Component {
  constructor(){
    super()
    this.state={
      weather,
      op_id :["one","two","three","four","five"],
      days : ["Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday"],
      date :  1580148000,////I am hardcoding a date because I'm working will old data... new Date().getTime()
      date_formated: "2020-01-27", ////I am hardcoding a date because I'm working will old data... new Date().
      today: "Monday"//Also hardcoding a day
    }
  }

  UNSAFE_componentWillMount = () =>{
    const rmv_old = this.state.weather.list.filter(item => item.dt>=this.state.date)
    this.setState({rmv_old})
  }

  componentDidMount = () =>{
    var three_pm = this.state.rmv_old.filter(item =>item.dt_txt.split(" ")[1]==="15:00:00")
    this.setState({three_pm})
  }

  render(){
    return (
      
      <div className="App">
        <Background info={this.state.rmv_old[0]}/>
        <div className="weather_widget">
          <Primary date_formated={this.state.date_formated} info={this.state.rmv_old[0]}/>
          {
          this.state.op_id.map((component,i)=> {
            return (
            <Secondary
              key={i}
              id={component}
              index={i}
              details={this.state.three_pm}
              day={this.state.days[this.state.op_id.indexOf(component)+1]}
            />
            )
          })
          }
          <Filler/>
        </div>
      </div>
    );
  }

}

export default App;

